.createPost{
 display:flex;
 align-items: center;
 justify-content: center;
 padding:24px;
}

.createPost__loggedIn{
    background-color:white;
    width:100%;
    max-width:650px;
    border:1px solid #e6e6e6;
    padding:12px 16px;
}
.createPost__imageUpload > input{
    display:none;
}

.createPost__uploadBtn {
    border:none;
    background-color:white;
    float: right;
    font-family: "Poppins";
    font-size: 95%;
    cursor:pointer;
}

.createPost__loggedInBottom{
    display:flex;
    justify-content: center;
}
.createPost__textarea{
    border:none;
    width: 100%;
    outline:none;
    margin-top: 6px;
    font-size: 14px;
    height: fit-content;
    resize: none;
    font-family: 'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.createPost__textarea :focus{
    outline:none;
}

#image-preview{
    display:none;
    height: 80px;
    margin: 12px 0px;
    border-radius:4px;

}