.signInBtn{
    background-color:#00d1b6;
    max-width:300px;
    text-align:center;
    padding:10px 18px;
    border-radius: 4px;
    color:white;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
}
.signInBtn:hover{
    box-shadow: 0px 2px 5px lightgray;
    transition: 0.2s;
}