@import url('https://fonts.googleapis.com/css2?family=Over+the+Rainbow&display=swap');
.navbar{
    justify-content: center;
    padding:12px 24px;
    background-color: white;
    border-bottom: 1px solid #eeeeee;
    display: flex;

}
.navbar__inner{
    width: 100vh;
    max-width: 1500px;
    background-color: transparent;
    justify-content: space-between;
    display:flex;
}
.navbar__img{
    height: 40px;
    border-radius: 50px;
}
.logo__img{
    width: 48px;
    height:38px;
}
.logo__text{
    font-size: 19px;
    font-family: "Poppins";
    font-weight: 400;
}