@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Over+the+Rainbow&display=swap);
*{
  margin:0px;
  padding:0px;
}

body {
  margin: 0;
  font-family:'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    background:#f8f9f8;
}
.signInBtn{
    background-color:#00d1b6;
    max-width:300px;
    text-align:center;
    padding:10px 18px;
    border-radius: 4px;
    color:white;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
}
.signInBtn:hover{
    box-shadow: 0px 2px 5px lightgray;
    transition: 0.2s;
}
.commentInput{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.commentInput__textarea{
    border:none;
    width:90%;
    height: 35px;
    resize: none;
    margin:8px 0px;
    font-family: "Poppins";
    background: white;
    border-radius: 3px;
    border-bottom: 2px solid lightgray;
}

.commentInput__textarea:focus{
    outline: none;
    border-bottom: 2px solid rgb(189, 189, 189);
    
}

.commentInput__btn{
    background-color: white;
    border:none;
    font-family: 'Poppins';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size:14px;
}
.home{
    background-color: #f8f9f8;
    height:100vh;
}
.navbar{
    -webkit-justify-content: center;
            justify-content: center;
    padding:12px 24px;
    background-color: white;
    border-bottom: 1px solid #eeeeee;
    display: -webkit-flex;
    display: flex;

}
.navbar__inner{
    width: 100vh;
    max-width: 1500px;
    background-color: transparent;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    display:-webkit-flex;
    display:flex;
}
.navbar__img{
    height: 40px;
    border-radius: 50px;
}
.logo__img{
    width: 48px;
    height:38px;
}
.logo__text{
    font-size: 19px;
    font-family: "Poppins";
    font-weight: 400;
}
.createPost{
 display:-webkit-flex;
 display:flex;
 -webkit-align-items: center;
         align-items: center;
 -webkit-justify-content: center;
         justify-content: center;
 padding:24px;
}

.createPost__loggedIn{
    background-color:white;
    width:100%;
    max-width:650px;
    border:1px solid #e6e6e6;
    padding:12px 16px;
}
.createPost__imageUpload > input{
    display:none;
}

.createPost__uploadBtn {
    border:none;
    background-color:white;
    float: right;
    font-family: "Poppins";
    font-size: 95%;
    cursor:pointer;
}

.createPost__loggedInBottom{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.createPost__textarea{
    border:none;
    width: 100%;
    outline:none;
    margin-top: 6px;
    font-size: 14px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    resize: none;
    font-family: 'Poppins',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.createPost__textarea :focus{
    outline:none;
}

#image-preview{
    display:none;
    height: 80px;
    margin: 12px 0px;
    border-radius:4px;

}
.post{
    background-color: white;
    max-width:650px;
    width:90vw;
    padding: 16px ;
    margin-bottom: 25px;
    font-family: "Poppins";
    border: 1px solid #e6e6e6;
}
.post__header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.post__headerLeft{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.post__profilePic{
    height:35px;
    border-radius:50px;
    margin-bottom: 10px;
}
.post__delete{
    border: none;
    background-color:white;
    cursor: pointer;

}
.post__delete:focus{
    outline:none;
}
.post__photoUrl{
    width:100%;
    object-fit:cover;
    border-radius: 4px;
}
.feed{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items:center;
            align-items:center
}
