.post{
    background-color: white;
    max-width:650px;
    width:90vw;
    padding: 16px ;
    margin-bottom: 25px;
    font-family: "Poppins";
    border: 1px solid #e6e6e6;
}
.post__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.post__headerLeft{
    display: flex;
    align-items: center;
}
.post__profilePic{
    height:35px;
    border-radius:50px;
    margin-bottom: 10px;
}
.post__delete{
    border: none;
    background-color:white;
    cursor: pointer;

}
.post__delete:focus{
    outline:none;
}
.post__photoUrl{
    width:100%;
    object-fit:cover;
    border-radius: 4px;
}